

















































































































































































































































































import pdf from "pdfvuer";
import Vue from "vue";
import * as UTIF from "utif";
const ROTATE_STEP = 90;
const ROTATE_MAX = 360;
const ZOOM_STEP = 0.1;
const ZOOM_MIN = 0.2;
const ZOOM_MAX = 2;
const MIN_WIDTH_STEP = 81.6;
const TIFF_TYPES = ["tiff", "tif"];
const IMAGE_TYPES = ["jpeg", "jpg", "gif", "png", "apng", "svg", "bmp", "ico"];
export default Vue.extend({
  name: "QDocumentViewer",
  components: {
    pdf,
  },
  props: {
    src: {
      default: "",
      type: [String, Object],
    },
    height: {
      type: String,
    },
    width: {
      type: String,
    },
    scale: {
      type: String,
    },
    showToolBar: {
      type: Boolean,
      default: true,
    },
    pdfToolbarBackgroundColor: {
      type: String,
      default: "#fafafa",
    },
    pdfBackgroundColor: {
      type: String,
      default: "#eeeeee",
    },
    hidePagination: {
      type: Boolean,
      default: false,
    },
    hideDescription: {
      type: Boolean,
      default: false,
    },
    hideDownload: {
      type: Boolean,
      default: false,
    },
    hideRotate: {
      type: Boolean,
      default: false,
    },
    hideZoom: {
      type: Boolean,
      default: false,
    },
    loadingContent: {
      type: String,
      default: "Loading...",
    },
    displayOnScroll: {
      type: Boolean,
      default: false,
    },
    fileDescription: {
      type: String,
    },
    showPrint: {
      type: Boolean,
      default: false,
    },
    _renderingProps: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      show: true,
      loadedRatio: 0,
      numPages: 0,
      page: 1,
      turn: 0,
      showToolbar: true,
      tiffLoading: false,
      zoomVal:
        this.scale != undefined && this.scale != null
          ? Number(parseFloat(this.scale).toFixed(2))
          : 1.0,
      rotateVal: 0,
      loadedPages: 1,
      docType: undefined,
      url: this.src,
      localLanguage: "en",
      pageTitle: "Page",
      pdfKey: 0,
      currentMinWidth: 816,
      dataType: "url" as "base64" | "arrayBuffer" | "url",
      tiffBufferData: null as ArrayBuffer | null,
      tifIfds: [] as any[],
    };
  },
  mounted() {
    this.docType = this.getType();
    let localLanguage = this._renderingProps
      .context()
      .GetItemSingle("Dory")
      .GetLRType()
      .substring(0, 2);
    if (localLanguage === "tr") {
      this.pageTitle = "Sayfa";
    } else {
      this.pageTitle = "Page";
    }
  },
  watch: {
    src: function () {
      this.pdfKey = new Date().valueOf();
      this.page = 1;
      this.loadedPages = 1;
      this.docType = this.getType();
    },
    scale: function () {
      this.zoomVal = Number(parseFloat(this.scale).toFixed(2));
    },
    page: function (newVal) {
      if (newVal === "") {
        return;
      }
      if (this.page > this.numPages) {
        this.page = this.numPages;
      }
      if (this.page < 1) {
        this.page = 1;
      }
      if (this.page <= this.numPages && this.page > this.loadedPages) {
        this.loadedPages = this.page;
      }
      if (this.docType === "tiff") this.renderTiffPage();
    },
    rotateVal(newVal) {
      if (newVal < 0) {
        this.rotateVal = 270;
      } else if (newVal >= 360) {
        this.rotateVal = 0;
      }

      if (this.docType === "tiff") this.renderTiffPage();
   },
  },
  created() {
    this.onFrameScrollBottom();
  },
  destroyed() {
    this.$el?.remove();
  },
  methods: {
    isTiffBuffer(buffer: ArrayBuffer): boolean {
      const bytes = new Uint8Array(buffer);
      return (
        (bytes[0] === 0x49 && bytes[1] === 0x49 && bytes[2] === 0x2A && bytes[3] === 0x00) || // little endian
        (bytes[0] === 0x4D && bytes[1] === 0x4D && bytes[2] === 0x00 && bytes[3] === 0x2A)    // big endian
      );
    },
    async loadTiff(dataType: "base64" | "arrayBuffer" | "url") {
      this.tiffLoading = true;
      this.tiffBufferData = await this.getTiffBuffer(dataType, this.src);
      this.tifIfds = UTIF.decode(this.tiffBufferData);
      this.numPages = this.tifIfds.length;
      this.$nextTick(() => {
        const canvas = this.$refs.tiffContainerRef as HTMLCanvasElement;
          if (!canvas) {
            setTimeout(() => {
              this.renderTiffPage();
              this.tiffLoading = false;
            }, 0);
        }
        else {
          this.renderTiffPage();
          this.tiffLoading = false;
        }
        
      });
   },
    async getTiffBuffer(type: string, src: any): Promise<ArrayBuffer> {
      if (type === "url") {
        const response = await fetch(src);
        return await response.arrayBuffer();
      }
      if (type === "base64") {
        const data = src.replace(/^data:image\/\w+;base64,/, "");
        return this.base64ToArrayBuffer(data);
      }
      return src;
    },
    renderTiffPage() {
      const canvas = this.$refs.tiffContainerRef as HTMLCanvasElement;
      if (!canvas) return;
      const scale = this.zoomVal || 1;
      const rotate = ((this.rotateVal % ROTATE_MAX) + ROTATE_MAX) % ROTATE_MAX;
      const pages = this.hidePagination ? this.tifIfds : [this.tifIfds[this.page - 1]];

      pages.forEach((ifd) => {
        UTIF.decodeImage(this.tiffBufferData, ifd);
        const rgba = UTIF.toRGBA8(ifd);
        const [w, h] = [ifd.width * scale, ifd.height * scale];
        if (rotate === 90 || rotate === 270) {
          canvas.width = h;
          canvas.height = w;
        } else {
          canvas.width = w;
          canvas.height = h;
        }
        const ctx = canvas.getContext("2d");
        if (ctx) {
          ctx.save();
          ctx.translate(canvas.width / 2, canvas.height / 2);
          ctx.rotate((rotate * Math.PI) / 180);
          ctx.translate(-w / 2, -h / 2); 
          const imageData = new ImageData(new Uint8ClampedArray(rgba), ifd.width, ifd.height);      

          createImageBitmap(imageData).then((bitmap) => { 
            // Clear canvas
            ctx.clearRect(0, 0, canvas.width, canvas.height);           
            ctx.drawImage(bitmap, 0, 0, w, h); 
            // Restore original state
            ctx.restore();            
          });          
        }
        //container.appendChild(canvas);
      });
    },

    base64ToArrayBuffer(base64: string): ArrayBuffer {
      const binary = window.atob(base64);
      const len = binary.length;
      const bytes = new Uint8Array(len);
      for (let i = 0; i < len; i++) bytes[i] = binary.charCodeAt(i);
      return bytes.buffer;
    },

    createPdf() {
      let takenSrc;
      let isDataBase64Encoded = false;
      if (this.src.startsWith("data:application/pdf;base64,")) {
        takenSrc = this.src.split("data:application/pdf;base64,")?.[1];
        isDataBase64Encoded = true;
      } else if (this.src.startsWith("data:application/pdf,")) {
        takenSrc = this.src.split("data:application/pdf,")?.[1];
        isDataBase64Encoded = true;
      } else {
        takenSrc = this.src;
        isDataBase64Encoded = false;
      }
      if (isDataBase64Encoded) {
        const blob = this.base64ToBlob(takenSrc, "application/pdf");
        takenSrc = URL.createObjectURL(blob);
      }
    
     this.url = takenSrc;
      pdf.createLoadingTask(takenSrc).then((pdf) => {
        this.numPages = pdf.numPages;
      }).catch((error) => {
           console.error("DocumentViewer createLoadingTask promise.catch..", error);
      });
    },
    downloadTiff() {
      let blobPromise: Promise<Blob>;
      if (this.dataType === "url") {
        blobPromise = fetch(this.src).then(res => res.blob());
      } else if (this.dataType === "base64") {
        const base64Data = this.src.replace(/^data:image\/tiff;base64,/, "");
        const buffer = this.base64ToAB(base64Data);
        blobPromise = Promise.resolve(new Blob([buffer], { type: "image/tiff" }));
      } else {
        blobPromise = Promise.resolve(new Blob([this.src], { type: "image/tiff" }));
      }

      blobPromise.then(blob => {
        const objectURL = URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = objectURL;
        link.download = this.fileDescription ?? "fileData.tiff";
        link.click();
        URL.revokeObjectURL(objectURL);
      });
    },
    base64ToAB(base64: string) {
      const binary_string = window.atob(base64);
      const len = binary_string.length;
      const bytes = new Uint8Array(len);
      for (let i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
      }
      return bytes.buffer;
    },
    base64ToBlob(base64, type = "application/octet-stream") {
      const binStr = atob(base64);
      const len = binStr.length;
      const arr = new Uint8Array(len);
      for (let i = 0; i < len; i++) {
        arr[i] = binStr.charCodeAt(i);
      }
      return new Blob([arr], { type: type });
    },
    focusSelectedPage() {
      if (!this.displayOnScroll) {
        return;
      }
      if (this.page > this.numPages) {
        this.page = 1;
        var focusedPage = document.getElementById("pdf-page-1");
        if (focusedPage) {
          focusedPage.scrollIntoView();
        }
      } else {
        var focusedPage = document.getElementById(
          "pdf-page-" + this.page.toString()
        );
        if (focusedPage) {
          focusedPage.scrollIntoView();
        }
      }
    },
    zoomIn() {
      if (this.zoomVal < ZOOM_MAX) {
        this.zoomVal += ZOOM_STEP;
        this.currentMinWidth += MIN_WIDTH_STEP;
        if (this.docType === "tiff") this.renderTiffPage();
      }
    },
    zoomOut() {
      if (this.zoomVal > ZOOM_MIN) {
        this.zoomVal -= ZOOM_STEP;
        this.currentMinWidth -= MIN_WIDTH_STEP;
        if (this.docType === "tiff") this.renderTiffPage();
      }
    },
    nextPage() {
      this.page += this.page < this.numPages ? 1 : 0;
    },
    previousPage() {
      this.page -= this.page > 0 ? 1 : 0;
    },
    rotate() {
      this.rotateVal = (this.rotateVal - ROTATE_STEP + ROTATE_MAX) % ROTATE_MAX;
      if (this.docType === "tiff") this.renderTiffPage();
    },
    goToPage(pageNumber: number) {
      if (pageNumber >= 1 && pageNumber <= this.numPages) {
        this.page = pageNumber;
      }
    },
    downloadPdf() {
      fetch(this.url)
        .then((res) => res.blob()) // Gets the response and returns it as a blob
        .then((blob) => {
          let objectURL = URL.createObjectURL(blob);
          var link = document.createElement("a");
          link.href = objectURL;
          link.download = this.fileDescription ?? "fileData"; //Name of the file to be downloaded
          link.dispatchEvent(new MouseEvent("click"));
        });
    },
    printPdf() {
      if (
        this.src.startsWith("data:application/pdf;base64,") ||
        this.src.startsWith("data:application/pdf,")
      ) {
        const printIframe = this.$refs.printIframe as HTMLIFrameElement;
        if (printIframe) {
          printIframe.src = this.url;
          printIframe.onload = () => {
            printIframe.contentWindow?.print();
          };
        }
      } else {
        fetch(this.src)
          .then((response) => response.blob())
          .then((blob) => {
            const objectURL = URL.createObjectURL(blob);
            const printIframe = this.$refs.printIframe as HTMLIFrameElement;
            if (printIframe) {
              printIframe.src = objectURL;
              printIframe.onload = () => {
                printIframe.contentWindow?.print();
                URL.revokeObjectURL(objectURL);
              };
            }
          });
      }
    },
    getType() {
      const val = this.src;
      if (val?.startsWith?.("data:")) {
        const mediaType = val
          .split(",")[0]
          .split(";")[0]
          .split(":")[1]
          .split("/");
        if (TIFF_TYPES.includes(mediaType[1])) {
          this.dataType = "base64";
          this.loadTiff(this.dataType);
          return "tiff";
        } else if (mediaType[0] === "image") {
          return "img";
        } else if (mediaType[1] === "pdf") {
          this.createPdf();
          return "pdf";
        }
        return mediaType[1];
      } else if (this.src instanceof ArrayBuffer) {
        if (this.isTiffBuffer(this.src)) {
          this.dataType = "arrayBuffer";
          this.loadTiff(this.dataType);
          return "tiff";
        }
        return;
      } else {
        if (!val || val === "") return;
        let parsedType = new URL(val).pathname.split(".").pop() || "";
        if (["html", "htm"].includes(parsedType)) {
          return "html";
        } else if (IMAGE_TYPES.includes(parsedType)) {
          return "img";
        } else if (
          parsedType === "pdf" ||
          parsedType?.replace(/\s/g, "").startsWith("pdf?")
        ) {
          this.createPdf();
          return "pdf";
        } else if (TIFF_TYPES.includes(parsedType)) {
          this.dataType = "url";
          this.loadTiff(this.dataType);
          return "tiff";
        }
        return parsedType;
      }
    },
    onFrameScrollBottom() {
      const self = this;
      let scrolledToBottom = (e) => {
        if (e.data == "scrolledToBottom") {
          self.$emit("scrolledToBottom");
        }
      };
      window.removeEventListener("message", scrolledToBottom, false);
      window.addEventListener("message", scrolledToBottom, false);
    },
    handleScroll({
      target: {
        scrollTop,
        clientHeight,
        scrollHeight,
        lastChild: { offsetHeight },
      },
    }) {
      if (this.displayOnScroll && this.showToolBar) {
        let pageVal = parseInt(
          Math.ceil(Math.round(scrollTop) / offsetHeight).toString()
        );

        this.page =
          pageVal == 0 ? 1 : pageVal >= this.numPages ? this.numPages : pageVal;
      }
      if (Math.round(scrollTop) + clientHeight + 1 >= scrollHeight) {
        if (this.displayOnScroll && this.numPages > this.loadedPages) {
          this.loadedPages++;
        }
        if (scrollTop > 0) {
          this.$emit("scrollEnd");
        }
      }
    },
  },
});
